// 新增
export const insertContract = (params) =>
  defHttp.post(
    {
      url: Api.insertUrl,
      params,
    },
    { isOnlyResult: true },
  );

// 修改
export const updateContract = (params) =>
  defHttp.put(
    {
      url: Api.updateUrl,
      params,
    },
    {},
  );

export const saveData = (params) => {
  return insertContract(params);
};

// 删除
export const deleteContract = (params) => defHttp.delete({ url: Api.deleteUrl + '?id=' + params });

export async function getDic(type: string) {
  const arr: any = [
    {
      dictType: '',
      value: '',
      label: '',
      colorType: '',
      cssClass: '',
    },
  ];
  const map = await dictStore.fetchBackendData(type, {
    type: type,
  });
  return arr.concat(map);
}

// 导出 Excel
export function exportExcel(params) {
  return defHttp.get({ url: Api.exportExcel, params });
}

export function initInfoItems(billTitleOption, item) {
  billTitleOption.infoItems.push({
    key: 'billCode',
    label: '单据编号',
    value: item.billCode,
    position: 'left',
  });
  billTitleOption.infoItems.push({
    key: 'fillinDate',
    label: '制单日期',
    value: item.fillinDate,
    position: 'center',
  });
  billTitleOption.infoItems.push({
    key: 'createPerson',
    label: '创建人',
    value: item.deptName + '.' + item.personMemberName,
    position: 'right',
  });
}

